.card{
    &, &-type-1, &-type-2, &-full{
        background-color: var(--White);
        text-decoration: none;
        display: block;
        transition: all .3s ease-out;
        width: 100%;
    }
    &-type-1, &-type-2, &-full{
        border: 1px solid var(--Gainsboro);
        position: relative;
        &:hover{
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, .15);
            transition: all .3s ease-out;
        }
    }
    &-type-1, &-type-2{
        .card-img{
            height: 190px;
        }
    }
    &-img{
        width: 100%;
        max-width: 100%;
        height: 260px;
        margin-bottom: 25px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    &-body{
        color: var(--mainColor);
        padding: 0 15px 20px;
        strong{
            line-height: 18px;
            margin-bottom: 7px;
            display: block;
        }
        p{
            margin: 0;
            font-size: 15px;
            line-height: 27px;
        }
        .Btn{
            margin-top: 30px;
        }
    }
    &-type-1{
        &::after{
            opacity: 0;
            position: absolute;
            content: "\E008";
            font-family: "icons";
            font-size: 10px;
            color: var(--Flamenco);
            top: 35px;
            right: 35px;
            transform: rotate(-90deg);
            transition: all .3s ease-out;
        }
        &:hover{
            .card-body{
                color: var(--mainColor);
                transition: all .3s ease-out;
            }
            &::after{
                right: 25px;
                opacity: 1;
                transition: all .3s ease-out;
            }
        }
        
        .card-body{
            padding-top: 20px;
            position: relative;
            font-size: 12px;
            text-transform: uppercase;
            text-align: center;
            color: var(--Charcoal);
            transition: all .3s ease-out;
            &::after{
                position: absolute;
                content: "";
                width: 50px;
                height: 3px;
                background-color: var(--Gainsboro);
                left: calc(50% - 25px);
                top: -3px;
            }
        }
    }
    &-type-2{
        .card-img{
            margin-bottom: 28px;
        }
        .card-body{
            font-size: 12px;
            text-transform: uppercase;
            text-align: center;
            padding-bottom: 20px;
            font-weight: 600;
            strong{
                font-weight: 700;
            }
        }
    }
    &-full{
        .card-img{
            height: 200px;
            margin-bottom: 10px;
        }
        .card-body{
            .card-link{
                color: var(--Charcoal);
                font-weight: 600;
                text-align: center;
                margin-bottom: 25px;
                text-decoration: none;
                display: block;
                font-size: 13px;
                line-height: 23px;
                transition: all .3s ease-out;
                &::after{
                    position: absolute;
                    content: "";
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
                &:hover{
                    color: var(--mainColor);
                    transition: all .3s ease-out;
                }
            }
            .price{
                margin-bottom: 25px;
            }
            .card-action{
                margin-bottom: 25px;
                button{
                    position: relative;
                    z-index: 1;
                }
                .popup-desc{
                    left: 0px;
                    top: -37px;
                    white-space: nowrap;
                }
            }
        }
        .card-bottom{
            position: absolute;
            width: calc(100% + 2px);
            background-color: #fff;
            border: 1px solid var(--Gainsboro);
            border-top: transparent;
            left: -1px;
            padding-bottom: 20px;
            top: calc(100% - 20px);
            z-index: 2;
            transition: all .3s ease-out;
            .card-action__bottom{
                button{
                    position: relative;
                    z-index: 1;
                    &:focus{
                        outline: none;
                    }
                }
                .buyFast{
                    margin-right: 10px;
                }
                .Btn-Wedgewood{
                    padding-left: 23px;
                    padding-right: 23px;
                    cursor: pointer;
                }
            }
            .count__wrap{
                opacity: 0;
                visibility: hidden;
                height: 0;
                margin-bottom: 0;
                transition: all .3s;
                button, input{
                    position: relative;
                    z-index: 1;
                }
            }
            .price{
                display: none;
            }
            .Instock{
                display: none;
            }
        }
        &:hover{
            .card-bottom{
                box-shadow: 0px 17px 15px 0px rgba(0, 0, 0, .15);
                transition: all .3s ease-out;
            }
            .count__wrap{
                opacity: 1;
                visibility: visible;
                height: 59px;
                margin-bottom: 25px;
                transition: all .3s;
            }
        }
    }
    &-Item{
        padding-bottom: 57px;
    }
}
.view-list{
    padding-bottom: 20px;
    .card-full{
        display: flex;
        padding: 20px 30px;
        &:hover{
            .card-bottom {
                box-shadow: none;
            }
        }
        .card-img{
            max-width: 240px;
            width: 100%;
            margin: 0 30px 0 0;
        }
        .card-bottom {
            width: auto;
            position: static;
            border: none;
            max-width: 400px;
            width: 100%;
            display: flex;
            align-items: center;
            background-color: transparent;
            padding-bottom: 0;
            margin-left: 20px;
            flex-shrink: 1;
            .count__wrap{
                opacity: 1;
                visibility: visible;
                height: auto;
                margin: 0 auto 0 0;
            }
            .card-bottom_block{
                margin-left: 20px;
            }
            .price{
                display: flex;
                justify-content: flex-start;
            }
            .Instock{
                display: block;
                line-height: normal;
                margin-top: 10px;
            }
            .card-action__bottom{
                margin-top: 20px;
            }
        }
        .card-body{
            display: flex;
            align-items: center;
            flex-direction: row-reverse;
            padding: 0;
            max-width: 580px;
            width: 100%;
            margin-right: auto;
            flex-shrink: 1;
            .price{
                display: none;
            }
            .card-action{
                margin-left: 20px;
                margin-bottom: 0;
            }
        }
        .card-link{
            max-width: 380px;
            width: 100%;
            text-align: left;
            margin-bottom: 0;
            margin-right: auto;
            flex-shrink: 1;
        }
        .card-body__wrap{
            display: flex;
            width: 100%;
            flex-shrink: 1;
        }
    }
}
@media(max-width: 1890px){
    .card-type-1 {
        .card-img{
            height: 10vw;
        }
    }
    .card-type-2 {
        .card-img{
            height: 9.6vw;
        }
    }
    .card-full .card-img {
        height: 10.15vw;
    }
}
@media(max-width: 1400px){
    .card-type-1{
        &::after{
            top: 20px;
            right: 20px;
        }
        &:hover{
            &::after{
                right: 15px;
            }
        }
    }
    .card-Item:not([data-card="list"]){
        flex: 0 0 33.3333333333%;
        max-width: 33.3333333333%;
    }
    .view-list {
        .card-full {
            padding: 15px;
            .card-img{
                margin-right: 15px;
                max-width: 200px;
            }
            .card-body {
                max-width: 382px;
                .card-action{
                    margin-left: 10px;
                }
            }
            .card-bottom {
                margin-left: 10px;
                .card-bottom_block{
                    margin-left: 10px;
                }
            }
            .card-link{
                max-width: 240px;
                margin-right: 0;
            }
        }
        .badge.text{
            right: 143px;
        }
    }
}
@media(max-width: 1199px){
    .card-type-1 {
        .card-img{
            height: 13.9vw;
            margin-bottom: 20px;
        }
        .card-body {
            padding-top: 8px;
            font-size: 13px;
            font-weight: 600;
            line-height: 19px;
            strong{
                font-weight: 600;
            }
            &::after{
                width: 24px;
                height: 2px;
                left: calc(50% - 12px);
            }
        }
    }
    .card-type-2 {
        .card-img{
            margin-bottom: 22px;
            height: 12.9vw;
        }
        .card-body{
            padding-bottom: 22px;
            padding-right: 0;
            padding-left: 0;
            font-size: 13px;
            line-height: 19px;
            strong{
                font-weight: 600;
            }
        }
    }
    .card-Item:not([data-card="list"]){
        flex: 0 0 25%;
        max-width: 25%;
    }
    .view-list {
        .card-full {
            .card-bottom{
                margin-left: 22px;
                max-width: 380px;
            }
        }
    }
    .card-full .card-img {
        height: 13.3vw;
    }
}
@media(max-width: 1024px){
    .card-Item:not([data-card="list"]){
        flex: 0 0 33.3333333333%;
        max-width: 33.3333333333%;
    }
    .card-full .card-img{
        height: 18.25vw;
    }
    .badge.text{
        padding: 0;
    }
    .card-full{
        .card-body{
            padding-bottom: 0;
        }
        .card-bottom{
            position: static;
            border: none;
            width: 100%;
            .count__wrap{
                height: auto;
                opacity: 1;
                visibility: visible;
                margin-bottom: 25px;
            }
        }
    }
    .card-Item {
        padding-bottom: 20px;
    }
}
@media(max-width: 991px){
    .view-list {
        .card-full{
            padding: 20px;
            .card-img{
                max-width: 240px;
                margin-right: 20px;
                height: 135px;
                margin-bottom: 50px;
            }
            .card-body__wrap{
                flex-direction: column;
                width: calc(100% - 260px);
            }
            .card-body{
                flex-direction: column;
                align-items: flex-start;
                max-width: 100%;
                margin-right: 0;
                justify-content: flex-start;
                .card-action{
                    margin-left: 0;
                    position: absolute;
                    left: 85px;
                    top: 165px;
                }
                .price{
                    display: flex;
                    margin-top: 17px;
                    margin-bottom: 0;
                }
            }
            .card-bottom{
                align-items: flex-end;
                width: 100%;
                margin-left: 0;
                .price{
                    display: none;
                }
                .count__wrap{
                    margin-right: 0;
                }
                .Instock{
                    transform: translateX(-120px);
                }
                .card-action__bottom{
                    margin-top: 20px;
                    margin-bottom: 22px;
                }
            }
             .card-link{
                 max-width: 100%;
             }
        }
    }
}
@media(max-width: 767px){
    .card-type-1 {
        .card-img{
            height: 107px;
        }
        .card-body {
            padding-bottom: 5px;
        }
    }
    .card-type-2 {
        .card-img{
           margin-bottom: 13px;
           height: 13.04vw;
        }
        .card-body{
           font-size: 11px;
           padding-bottom: 10px;
        }
    }
    .card-full {
        .card-body {
            .card-action{
                margin-bottom: 15px;
            }
            .card-link{
                margin-bottom: 15px;
            }
            .price{
                margin-bottom: 18px;
            }
        }
        .card-bottom {
            .count__wrap{
                margin-bottom: 15px;
            }
        }
    }
    .card-Item {
        padding-bottom: 10px;
    }
    .view-list .card-full .card-link{
        margin-bottom: 0;
    }
}
@media(max-width: 750px){
    .card-Item:not([data-card="list"]){
        flex: 0 0 50%;
        max-width: 50%;
    }
    .card-full .card-img {
        height: 28.25vw;
    }
}
@media(max-width: 700px){
    .view-list {
        .card-full {
            .card-img{
                height: 195px;
            }
            .card-body {
                .card-action{
                    top: 223px;
                }
            }
            .card-bottom {
                position: relative;
                top: 0;
                right: 0;
                left: auto;
                .count__wrap{
                    position: absolute;
                    bottom: 45px;
                    left: 0;
                }
                .Instock{
                    transform: translateX(0);
                }
                .card-action__bottom{
                    margin-bottom: 0;
                    margin-top: 90px;
                }
                .card-bottom_block{
                    margin-left: 0;
                }
            }
        }
    }
}

@media(max-width: 575px){
    .card-type-1 {
        .card-img{
            height: 27.9vw;
        }
        .card-body {
            padding-bottom: 5px;
            
        }
        &::after{
            top: 15px;
            right: 15px;
            font-size: 6px;
        }
        &:hover{
            &::after{
                right: 10px;
            }
        }
    }
    .card-type-2 {
        .card-body {
            font-size: 11px;
        }
    }
    .card-Item:not([data-card="list"]){
        flex: 0 0 100%!important;
        max-width: 100%!important;
    }
    .card-full .card-img {
        height: 51.25vw;
    }
}